import React from 'react';
import { Link } from 'react-router-dom';

import EstruturaPagina from '../../componentes/EstruturaPagina';

import './estilo.css';


const Missao = () => {
    return (
        <EstruturaPagina>
            <div className="missao-consultorio">
                <div className="missao-container"></div>
                <h1>Missão, Visão e Valores em odontologia</h1>
                <ul className="missao-lista">
                    <li>
                        <p>Atualmente nosso país passa por um ciclo de mudanças no cenário econômico e o segmento de prestação de serviços odontológicos encontra-se dentro deste processo, precisando reformular algumas estratégias. Dentistas dentro de seus consultórios ou atuando na direção de clínicas procuram melhores desempenhos provocados por um mercado cada vez mais competitivo e com um perfil de cliente cada vez mais exigente. Torna-se necessário criar uma identidade própria que caracterize e diferencie seu serviço dos demais concorrentes.

                        Para isso, devemos pensar na importância de se definir referenciais de Missão, Visão e Valores. São um conjunto de declarações que orientam a formulação de estratégias e as diretrizes do negócio. O dentista precisa ter em seu negócio objetivos claros e metas definidas para que sua carreira possa prosperar. Definir estes referenciais, também chamados de princípios norteadores, é poderoso para o planejamento da carreira.  É o pensamento estratégico em odontologia aplicado no seu modelo de negócio visando direcionar seu caminho e definir sua marca.

                        A Missão de uma empresa pode ser considerada como a razão de existir da empresa ou o motivo principal de sua existência. Deve contemplar onde está inserido seu negócio, objetivos financeiros, sociais e como ela quer ser reconhecido. Tende a ser um referencial mais estável e só costuma ser alterado quando empresa sofre algum tipo de alteração como expansão ou mudança de segmento de atuação. Uma forma assertiva de definir uma missão é responder aos questionamentos básicos a seguir:</p>
                    </li>
                    <li>
                        <p>– Por que seu consultório/clínica existe?.</p>
                    </li>
                    <li>
                        <p>– O que seu consultório/clínica faz?</p>
                    </li>
                    <li>
                        <p>– Para quem?</p>
                    </li>
                    <li>
                        <p><b>Exemplos de Missão de clínicas de saúde:</b></p>
                    </li>
                    <li>
                        <p>“Preservar a saúde, o sorriso e a qualidade de vida das pessoas, a partir de um atendimento profissional, ético e personalizado. ”<br></br>

                        “Promover com excelência a ética e a saúde bucal, valorizando a participação multiprofissional, recursos tecnológicos e visando a satisfação do cliente. ”<br></br>

                        “Disponibilizar aos clientes os melhores, mais tecnológicos e mais rápidos tratamentos dentários para garantir saúde e satisfação ao sorrir. ”<br></br>

                        “Prestar serviços de implantodontia e assegurar confiança, precisão e rapidez no diagnóstico e tratamento. ”<br></br>

                        “Oferecer soluções em saúde bucal, por meio de um serviço voltado para o bem-estar do cliente. ”</p>
                    </li>
                </ul>
            </div>
        </EstruturaPagina>
    );
}

export default Missao;