import React from 'react';
import './estilo.css';

import EstruturaPagina from '../../componentes/EstruturaPagina';

const Agenda = () => {
    return(
        <EstruturaPagina>
        <div className="formulario-container">
            <section>
            <h1 className="title-formulario">CONQUISTE O SORRISO DOS SEUS SONHOS<br></br>
            AGENDE UMA AVALIAÇÃO!</h1>
            
            <form className="formulario">
                <ul className="lista-formulario">
                    <li>
                        <label>Nome:</label>
                        <input type="text" placeholder="Nome Completo" />
                    </li>

                    <li>
                        <label>Email:</label>
                        <input type="email" placeholder="E-mail" />
                    </li>

                    <li>
                        <label>CPF:</label>
                        <input type="text" placeholder="000.000.000-00"/>
                    </li>

                    <li>
                        <label>Telefone:</label>
                        <input type="tel" placeholder="11 5669-5669"/>
                    </li>

                    <li>
                        <label>Data de nascimento:</label>
                        <input type="date"/>
                    </li>

                    <li>
                        <label>Gênero:</label>
                        <select>
                            <option>Masculino</option>
                            <option>Feminono</option>
                        </select>
                    </li>

                    <li>
                        <label>CEP:</label>
                        <input type="text" placeholder="00000-000" />
                    </li><br></br>
                    <div class="cta">
							<p className="text-title"><a href="/agenda" class="btn-agenda">Agendar</a></p>
					</div>
                </ul>
            </form>
            </section>
        </div>
        
        </EstruturaPagina>
    );
}

export default Agenda;